.die-container {
  height: 50px;
  width: 50px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0);
  border-radius: 10px;
  align-items: center;
  display: flex;
  justify-content: center;
  background: white;
  cursor: pointer;
  margin: 50px auto auto auto;
}
.die-container.held {
  background-color: #59E391;
}

.die-value {
  font-size: 2rem;
  font-family: Georgia, "Times New Roman", Times, serif;
  margin: auto;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background: #0B2434;
  padding: 20px;
  font-family: "Karla", sans-serif;
}

main {
  margin: 50px auto auto auto;
  border: 10px solid black;
  background: rgb(197, 194, 194);
  width: 50%;
  height: 80vh;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.title {
  font-size: 40px;
  margin: 0;
  text-align: center;
}

.instructions {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  margin-top: 0;
  text-align: center;
}

.app-container {
  display: grid;
  grid-template: auto auto/repeat(5, 1fr);
  gap: 20px;
}

.roll-btn {
  height: 50px;
  width: 150px;
  border: none;
  border-radius: 6px;
  background-color: #5035FF;
  color: white;
  font-size: 1.2rem;
  font-family: "Karla", sans-serif;
  cursor: pointer;
  margin: auto;
}

.roll-btn:focus {
  outline: none;
}

.roll-btn:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}/*# sourceMappingURL=main.css.map */